/*
*  Menu active links
*
*/

jQuery(function ($){
    
    let location = window.location.href;
    let cur_url  = location.split('/').pop();
    let lastId;
    let header        = $("#masthead");
    let topMenu       = header.find('.menu'),
        topMenuHeight = header.outerHeight()+15,
        menuItems     = topMenu.find("a.inside-link"),
        menuItemsAll  = topMenu.find('a');
    
    if( menuItemsAll.length ){
        LoadActiveLinks( topMenu, menuItemsAll );
    }
    if( menuItems.length ){
        ScrollActiveLinks( menuItems, menuItemsAll, topMenuHeight );   
    }
    
    function ScrollActiveLinks( menuItems, menuItemsAll, topMenuHeight ){
        
        try{
            let isResizeble = true;
            let scrollItems   = menuItems.map(function(){
                  let item = $($(this).attr("href").split('/').pop());
                  if (item.length){ 
                      return item; 
                  }
            });
            $(window).one('mousewheel', function(){
                if(isResizeble) {
                    menuItemsAll.removeClass("current-link").parent().filter('.menu-item-type-custom').removeClass("current-menu-item");
                    isRezeble = false;
                }
            });
            $(window).on('scroll', function(){
                setTimeout(
                    function() { menuItemsAll.blur() }, 1000
                );

                let fromTop = $(this).scrollTop()+topMenuHeight;
                var cur     = scrollItems.map(function(){
                    if ($(this).offset().top < fromTop){
                        return this;
                    }
                });
                cur    = cur[cur.length-1];
                let id = cur && cur.length ? cur[0].id : "";
                if( id === ""){
                    menuItems.removeClass("current-link").parent().removeClass("current-menu-item");
                }
                if(lastId !== id){
                   lastId = id;
                   menuItems.removeClass("current-link").parent().removeClass("current-menu-item").end().filter("[href='#"+id+"']").addClass('current-link').parent().addClass("current-menu-item");
                }
            });    
        }
        catch(e){
            console.log('Problem with menu Scroll Active Links script');
        }
        
    }
    
    function LoadActiveLinks( topMenu, menuItemsAll ){
       try{
            let isResizeble = true;
            if(isResizeble) {
                menuItemsAll.removeClass("current-link").parent().filter('.menu-item-type-custom').removeClass("current-menu-item");
                isRezeble = false;
            }
            topMenu.find('li').each(function(){
                let link = $(this).find('a').attr('href');
                if(cur_url == link){
                    $(this).find('a').addClass('current-link');
                }
            });
       }
       catch(e){
           console.log('Problem with menu Load Active Links script');
       }
    }
});
function SectionCount(){
    const section = $(document).find('section');
    section.eq(0).addClass('main-section');
    let counter = 1;
    section.each(function(){
            name = 'section-' + counter;
            $(this).attr('data-id', name );
            counter++;
    });
    try{
        Revealator.effects_padding = '-700';
        section.eq(0).addClass('main-section');
        section.not('.main-section').filter(':odd').addClass('odd revealator-slideup revealator-once revealator-delay1');
        section.not('.main-section').filter(':even').addClass('even revealator-zoomin revealator-once revealator-delay1');   
    }
    catch(e){
        console.log('Revealator not included');
    }
}

function smoothScroll(){
	$("a[href^='#']").on('click', function(event) {
		if (this.hash !== "") {
			event.preventDefault();
			if (this.hash == "#masthead") {
				$('html, body').animate({
					scrollTop: 0
				}, 800, function(){
				});
			} else {
				let hash = this.hash;
				$('html, body').animate({
					scrollTop: $(hash).offset().top
				}, 800, function(){
				});
			}
		};
	});
}

function slideDown(){
    let counter = 1;
    $('section').each(function(){
        counter++;
        let toDown          = $(this).find('.arrow-to-next');
        let sectionNextName = 'section[data-id="section-' + counter + '"]';
        let sectionNextId   = $(sectionNextName).attr('id');
        toDown.attr('href', '#' + sectionNextId);
    });
}

/*
 * Helper function
 * return boolean
 */
function isset(variable) {
    return typeof variable !== 'undefined';
}

/*
 * Helper function
 * return boolean
 */
function is_empty(variable) {
    return variable === '';
}

/*
 * Helper function
 * return boolean
 */
function is_null(variable) {
    return variable === null;
}

/*
 * Helper function
 * return boolean
 */
function is_num(variable) {
    return typeof variable === 'number';
}

/*
 * Helper function
 * return boolean
 */
function is_string(variable) {
    return typeof variable === 'string';
}

/*
 * Helper function
 * return boolean
 */
function is_bool(variable) {
    return typeof variable === 'boolean';
}

/*
 * Helper function
 * return boolean
 */
function is_arr(variable) {
    return Array.isArray(variable);
}

/*
 * Helper function
 * return boolean
 */
function is_obj(variable) {
    if( Array.isArray(variable) === true || variable === null ) {
      return false;
    }
    else {
      return typeof variable === 'object';
    }
}

/*
 * Helper function
 * return boolean
 */
function is_func(variable) {
    return typeof variable === 'function';
}

jQuery( document ).ready(function($) {
  SectionCount(); // Count sections
  smoothScroll();
  slideDown(); // Slide to next section
});
